@use "../variables.scss" as *;

.social__link {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 48px;
  height: 48px;
  background-color: #81b3d2;

  &:hover {
    background-color: $blue-58;
  }

  &:active {
    background-color: $blue-54;

    .social__icon {
      opacity: 0.3;
    }
  }
}
